import { fetchRawJson } from 'spatial-id-svc-base';
/** 環境設定を取得します */
export const getConfiguration = async ({ baseUrl, abortSignal }) => {
    return await fetchRawJson({
        method: 'GET',
        baseUrl,
        path: '/search_path/configuration',
        abortSignal,
    });
};
/** 障害物マップを一覧します */
export const listBarrierMaps = async ({ baseUrl, payload, abortSignal }) => {
    return await fetchRawJson({
        method: 'POST',
        baseUrl,
        path: '/search_path/barrier_maps',
        payload,
        abortSignal,
    });
};
/** 測地座標をキーに変換します。境界の場合は最小のKeyが返ります */
export const convertGeodeticToKey = async ({ baseUrl, payload, abortSignal, }) => {
    return await fetchRawJson({
        method: 'POST',
        baseUrl,
        path: '/search_path/geodetic_to_key',
        payload,
        abortSignal,
    });
};
